/* _portfolio-slideshow.scss */
#edit_with_polarr_floater {
    display:none;
}

.ls-wrapper {
    border:1px solid #ddd;
    background:#f5f5f5;
    margin:$offset-g2 auto;

    [class$="-arrow"] {
        // width: 25px;
        // height: 25px;
        // background-image: url(../images/admin_images/arrow.dark.png);
        // background-repeat: no-repeat;
        // margin-top: 20px;
        // position: relative;
        background-size: 200%;
    }

    .ls-nav-left-arrow {
        &:before {
            content: "\f053" !important;
        }
    }

    .ls-nav-right-arrow {
        &:before {
            content: "\f054" !important;
        }
    }

    .ls-nav-left-arrow,
    .ls-nav-right-arrow {
        // margin:$offset-1;
        margin: 0;
        padding:$offset-g1 !important;
        width: $height-disc-button;
        height: $height-disc-button;
        top: 40%;
        background:black !important;
        background:rgba(0,0,0,0.5) !important;

        &:before {
            @extend .fa-dummy;
            text-align:center;
            color:white;
        }

        &:hover {
            background:white !important;

            &:before {
                color:$color-primary;
            }
        }
    }

    .portfolio-gallery-content {
        background:transparent !important;
        
        .slider-content {
            background:transparent !important;

            > .panel-wrapper {
                padding:0;

                > .slider-content-wrapper {
                    > div {
                        &:not(.right-block) {
                            margin:0 !important;
                            padding:0 !important;
                        }

                        &.right-block {
                            > div {
                                margin:0 !important;
                                padding:$offset-g1 !important;

                                > h2 {
                                    padding:0 !important;
                                    margin:0 !important;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    
}